.img__bd {
    position: relative;
    z-index: 2;
}
.img__bd:before {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: calc(100% - 130px);
    content: "";
    left: 50%;
    bottom: 0;
   
    transform: translateX(-50%);
    background-color:#0a1118;
    box-shadow: 10px 10px 19px #1c1e22, -10px -10px 19px #262a2e;
    border-radius: 6px;
}
.personel__img {
    border: 0;
   vertical-align: middle; 
    max-width: 100%;
    height: 540px;
}
.subtitle {
