    .main-menu {
    background:#212428;
    border-right:1px solid #e5e5e5;
    position:fixed;
    top:0;
    bottom:0;
    height:100%;
    right:0;
    width:100px;
    overflow:hidden;
  
    transition:width .05s linear;
    box-shadow: 10px 10px 19px #1c1e22, -10px -10px 19px #262a2e;
    z-index:1000;
    }
    
    .main-menu>ul {
        margin: 7px 0;
    position: relative;
    top: 50%;
    left: 50%;
    
    transform: translate(-50%, -50%);
    }
    
    .main-menu li {
    position:relative;
    display:block;
    width:250px;
    box-shadow: 10px 10px 19px #1c1e22, -10px -10px 19px #262a2e;
    }
    
    .main-menu li {
        transform: translateY(-5px);
    }
    
    .main-menu li>a {
    position:relative;
    display:table;
    border-collapse:collapse;
    border-spacing:0;
    color:#999;
     font-family: arial;
    font-size: 14px;
    text-decoration:none;
    
    transition:all .1s linear;
      
    }
    
    .main-menu .nav-icon {
    position:relative;
    display:table-cell;
    width:60px;
    height:36px;
    text-align:center;
    vertical-align:middle;
    font-size:18px;
    }
    
